import {
    CLOSE_MODAL,
    OPEN_MODAL
} from './modals-constants'
import { ICloseModal, IOpenModal } from './modals-types'


//  
export const openModal = ():IOpenModal => {
    return {
        type: OPEN_MODAL
    }
}


//  
export const closeModal = ():ICloseModal => {
    return {
        type: CLOSE_MODAL
    }
}