import React from 'react';
import OrderDetailsStyles from './order-details.module.css';
import { CheckMarkIcon } from '@ya.praktikum/react-developer-burger-ui-components';
import { useSelector } from '../../utils/hooks';

export const OrderDetails = () => {
    //    
    const {
        order
    } = useSelector(store => store.ingredientsStore)

    return (

        <div className={OrderDetailsStyles.orderDetails}>
            <div className={OrderDetailsStyles.container}>
                <h2 className={`text text_type_digits-large ${OrderDetailsStyles.orderNumber}`}>{!order?.order ? "..." : order.order.number}</h2>
                <p className={`text text_type_main-medium ${OrderDetailsStyles.orderId}`}> </p>
                <div className={`text text_type_main-medium ${OrderDetailsStyles.checkMarkIcon}`}><CheckMarkIcon type="primary" /></div>
                <p className={`text text_type_main-default ${OrderDetailsStyles.startCooking}`}>   </p>
                <p className={`text text_type_main-default text_color_inactive ${OrderDetailsStyles.wait}`}>    </p>
            </div>

        </div>

    )
};